.RootContainer{
  margin: 0px;
  padding: 0px;
  width: 100vw;
  height: 100vh;
  background: url("https://img.freepik.com/free-photo/musical-background-with-musical-keys-white-flat-lay-copy-space_169016-16401.jpg?w=996&t=st=1694810691~exp=1694811291~hmac=44184c8b5697c1750e44991fd5a6adc7c612a3acfee369e5875d7648df3c235e");
  background-size: cover;
}
@media (max-width: 768px) {
  .RootContainer {
    background: #FEF8DD;
    background-size:cover; /* Adjust this value as needed */
   
  }
}
#first{
padding: 10px;
flex: 4;
display: flex;
align-content: center;
flex-direction: column;
justify-content: center;
gap: 10px;
}
#second{
flex: 6;
}
h1{
  text-align: center;
  font-size: 5em;
  flex:2;
  font-family: Helvetica, sans-serif;
}
#contain{
  flex: 2;
  flex-direction: column;
  display: flex;
  align-content: center;
  justify-content: center;
  flex-wrap: wrap;
  display: flex;
  gap: 30px;
}
#input{
  flex:   1;
  display: flex;
  align-content: center;
  justify-content: center;
  flex-wrap: wrap;
  text-align: center;
}
input{
  padding-left: 50px;
}
#button{
  flex: 1;
  display: flex;
  align-content: center;
  justify-content: center;
}